<template>
	<div class="bind">
		<div class="input-wrap">
			<div class="input-item">
				<label for="inpt-name">姓名</label>
				<input type="text" v-model="name" id="inpt-name"/>
				<span class="close" @mouseup="clear('name')" v-show="name!==''"></span>
			</div>
			<div class="input-item">
				<label for="inpt-sex">性别</label>
				<input type="text" v-model="sex" id="inpt-sex"/>
				<span class="close" @mouseup="clear('sex')" v-if="sex!==''"></span>
			</div>
			<div class="input-item">
				<label for="inpt-idNum">身份证号</label>
				<input type="text" v-model="idNum" id="inpt-idNum"/>
				<span class="close" @mouseup="clear('idNum')" v-if="idNum!==''"></span>
			</div>
			<div class="input-item">
				<label for="inpt-securityNumber">医保账号</label>
				<input type="text" v-model="securityNumber" id="inpt-securityNumber"/>
				<span class="close" @mouseup="clear('securityNumber')" v-if="securityNumber!==''"></span>
			</div>
			<div class="input-item">
				<label for="inpt-cardNum">医保卡号</label>
				<input type="text" v-model="cardNum" id="inpt-cardNum"/>
				<span class="close" @mouseup="clear('cardNum')" v-if="cardNum!==''"></span>
			</div>
		</div>
		<div class="tip">医保账号：社会保障卡上的保险号<br />
医保卡号：社会保障卡上的卡号</div>
		<div class="error">
			<!-- 目前仅支持绑定福州的医保账号 -->
			<span v-if="error" v-text="error"></span>
		</div>
		<div class="submit" :class="{isSubmit: isSubmit}" @click="submit">提交</div>
	</div>
</template>

<script>
import {mapMutations} from 'vuex';
export default {
	data(){
		return {
			name: '',
			idNum: '',
			securityNumber: '',
			cardNum: '',
			sex: '',
			error: ''
		}
	},
	methods:{
		...mapMutations({
			
		}),
		clear(key){
			this[key] = '';
		},
		submit(){
			if(!this.isSubmit){return}
			this.$api.bind.bindSubmit({
				name: this.name,
				idNum: this.idNum,
				cardNum: this.cardNum,
				securityNumber: this.securityNumber,
				sex: this.sex
			}).then(res=>{
				console.log('submitres',res);
			})
		}
	},
	computed: {
		isSubmit(){
			return this.securityNumber!=='' && this.cardNum!==''
		},
	},
	components:{
		
	}
}
</script>

<style lang="scss" scoped="scoped">
@import "@/assets/styles/mixin.scss";
.bind{
	.input-wrap{
		padding-left: pxToRem(32px);
		background: #fff;
		.input-item{
			position: relative;
			border-bottom: pxToRem(2px) solid #EDEDED;
			height: pxToRem(100px);
			color: #222;
			box-sizing: content-box;
			background: #fff;
			font-size: 0;
			label{
				line-height: pxToRem(100px);
				font-size: pxToRem(32px);
				display: inline-block;
				width: pxToRem(188px);
				text-align: left;
			}
			input{
				display: inline-block;
				height: pxToRem(100px);
				width: pxToRem(525px);
				padding: pxToRem(32px) 0;
				line-height: pxToRem(36px);
				font-size: pxToRem(32px);
				border: 0px;
				outline: none;
				box-sizing: border-box;
				margin: 0;
				background: transparent;
			}
		}
		.close{
			display: block;
			position: absolute;
			content: '';
			background-image: url('../../assets/images/input-close.png');
			background-size: 100%;
			background-position: center;
			background-repeat: no-repeat;
			z-index: 1;
			right: pxToRem(25px);
			top: 50%;
			margin-top: pxToRem(-22px);
			width: pxToRem(44px);
			height: pxToRem(44px);
		}
	}
	.tip{
		margin: pxToRem(17px) pxToRem(32px);
		line-height: pxToRem(36px);
		font-size: pxToRem(24px);
		color: #8E8E93;
	}
	.error{
		text-align: center;
		color: #DF3031;
		font-size: pxToRem(24px);
		margin: pxToRem(-2px) 0 pxToRem(24px);
		line-height: pxToRem(28px);
		height: pxToRem(28px);
	}
	.submit{
		line-height: pxToRem(88px);
		width: pxToRem(654px);
		margin: pxToRem(15px) auto;
		color: #fff;
		border-radius: pxToRem(3px);
		text-align: center;
		background: rgba($color:  #DF3031, $alpha: .5);
		&.isSubmit{
			background: #DF3031;
		}
	}
}
</style>
